body {
  padding: 15px 0;
}

button {
  color: #6b5770;
  z-index: 0;
  box-sizing: border-box;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  touch-action: manipulation;
  background-image: linear-gradient(90deg, #fd7f34, #bd155b);
  border: 1px solid;
  border-radius: 5px;
  outline: none;
  margin: 0 15px 15px 0;
  padding: 14px 30px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

button:before {
  content: "";
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  z-index: -2;
  background: linear-gradient(90deg, #fd7f34, #bd155b);
  border-radius: 5px;
  transition: all .4s;
  position: absolute;
  top: -2px;
  left: -2px;
}

button:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(90deg, #fff, #fff);
  border-radius: 4px;
  transition: all .4s;
  position: absolute;
  top: 0;
  left: 0;
}

button:hover {
  color: #fff;
  transition: all .3s;
}

button:hover:after {
  background: linear-gradient(90deg, #fd7f34, #bd155b);
}

button:active:after {
  background: linear-gradient(90deg, #d96d2d, #760f3a);
}

button:focus-visible {
  box-shadow: 0 0 0 3px #fd7f34;
}

button:disabled {
  pointer-events: none;
}

button:disabled:before {
  filter: grayscale();
}

